import React, {Component} from 'react';

import Messenger from '../core/Messenger';
import Menu from './Menu';
import Info from './Info';
import Account from './Account';
import Password from './Password';


class ProfileInfo extends Component{
    constructor(props){
        super(props);
        this.state = {
            user:{},
            tab:'info'
        };
    }
    render(){
        let {user} = this.state;
        let {tab} = this.state;
        let {events} = this.props;
        let form = tab === 'info' ? <Info onChangeInfo={this.changeInfo} events={events} /> : (
            tab === 'account' ? <Account events={events} /> : (
                tab === 'password' ? <Password events={events} /> : ""
            )
        );
        let profile = (!user.access_token)?(<h3>Đang xử lý...</h3>):(
            <div className="row">
                <div className="col-sm-6 col-md-5 col-lg-4 col-xl-3">
                    <div className="avatar text-center">
                        <img src={user.avatar} alt={user.name} />
                    </div>
                    <div className="name">
                        <h3 className="text-center">{user.name}</h3>
                    </div>
                    <div className="menu mt-2">
                        <Menu active={tab} onSelectTab={this.selectTab} />
                    </div>
                </div>
                <div className="col-sm-6 col-md-7 col-lg-8 col-xl-9">
                    {form}
                </div>
            </div>
        );
        return (
            <div className="profile-info container">
                {profile}
            </div>
            
        );
    }

    componentDidMount(){
        this.ioEventListener();
        if(Messenger.checkLogin()){
            this.setState({user:Messenger.user});
        }else if(Messenger.access_token){
            this.emit('verify-account', {
                data:{
                    access_token:Messenger.access_token,
                },
                success:'check-profile-info-success',
                fail:'check-profile-info-fail',
            });
        }else{
            this.props.history.push('/login');
        }
    
        
    }

    emit = (event, data) => {
        Messenger.socket().emit(event, data);
    }

    
    ioEventListener = () => {
        const socket = Messenger.socket();
        // dang nhap thanh cong
        socket.on('check-profile-info-success', (user) => {
            let {access_token} = user;
            Messenger.set({user, access_token:access_token});
            this.setState({user});
        });

        socket.on('check-profile-info-fail', (data) => {
            this.props.history.push('/login');
        });
    }

    changeInfo = (user) => {
        this.setState({user});
    }

    selectTab = (tab) => {
        this.setState({tab});
    }

    
}

export default ProfileInfo;


