import React, {Component} from 'react';

import Messenger from '../core/Messenger';
import Menu from './Menu';


export default class Account extends Component{
    constructor(props){
        super(props);
        this.state = {
            user:{},
            form:{
                access_token:"",
                username:"",
                email: "",
                password:""
                
            }
        };
    }
    render(){
        let {form} = this.state;
        let {user} = this.state;
        let profile = (!user.access_token)?(<h3>Đang xử lý...</h3>):(
            <div className="profile-form form">
                <div className="row">
                    <div className="col-sm-12 col-md-5 col-lg-4 col-xl-3">
                        <label htmlFor="profile-username">Tên truy cập</label>
                    </div>
                    <div className="col-sm-12 col-md-7 col-lg-8 col-xl-9">
                        <div className="form-group">
                            <div className="form-control-input input-group">
                                <div className="input-group-prepend">
                                    <label htmlFor="profile-username" className="input-group-text"><i className="fa fa-user"></i></label>
                                </div>
                                <input 
                                    type="text" 
                                    name="username" 
                                    id="profile-username" 
                                    onChange={this.inputDidUpdate} 
                                    onKeyUp={this.inputDidUpdate} 
                                    className="form-control" 
                                    placeholder="Nhập tên truy cập" 
                                    value={form.username} 
                                />
                            </div>
                        </div>
                    </div>
                </div>
                
                <div className="row">
                    <div className="col-sm-12 col-md-5 col-lg-4 col-xl-3">
                        <label htmlFor="profile-email">Email</label>
                    </div>
                    <div className="col-sm-12 col-md-7 col-lg-8 col-xl-9">
                        <div className="form-group">
                            <div className="form-control-input input-group">
                                <div className="input-group-prepend">
                                    <label htmlFor="profile-email" className="input-group-text"><i className="fa fa-envelope"></i></label>
                                </div>
                                <input 
                                    type="text" 
                                    name="email" 
                                    id="profile-email" 
                                    onChange={this.inputDidUpdate} 
                                    onKeyUp={this.inputDidUpdate} 
                                    className="form-control" 
                                    placeholder="Nhập email" 
                                    value={form.email} 
                                />
                            </div>
                        </div>
                    </div>
                </div>
                
                <div className="row">
                    <div className="col-sm-12 col-md-5 col-lg-4 col-xl-3">
                        <label htmlFor="profile-password">Mật khẩu hiện tại</label>
                    </div>
                    <div className="col-sm-12 col-md-7 col-lg-8 col-xl-9">
                        <div className="form-group">
                            <div className="form-control-input input-group">
                                <div className="input-group-prepend">
                                    <label htmlFor="profile-password" className="input-group-text"><i className="fa fa-key"></i></label>
                                </div>
                                <input 
                                    type="password" 
                                    name="password" 
                                    id="profile-password" 
                                    onChange={this.inputDidUpdate} 
                                    onKeyUp={this.inputDidUpdate} 
                                    className="form-control" 
                                    placeholder="Nhập Mật khẩu hiện tại" 
                                    value={form.password} 
                                />
                            </div>
                        </div>
                    </div>
                </div>
                
                
                <div className="form-group buttons text-center">
                    <button type="button" className="btn btn-success" onClick={this.save}><i className="fa fa-pencil"></i> Lưu lại</button>
                </div>
            </div>
        );
        return (
            <div className="profile-info mt-3">
                {profile}
            </div>
            
        );
    }

    componentDidMount(){
        this.ioEventListener();
        if(Messenger.checkLogin()){
            let {user} = Messenger;
            let {form} = this.state;
            form.access_token = user.access_token;
            form.username = user.username;
            form.email = user.email;
            this.setState({form, user});
        }else if(Messenger.access_token){
            this.emit('verify-account', {
                data:{
                    access_token:Messenger.access_token,
                },
                success:'check-profile-account-success',
                fail:'check-profile-account-fail',
            });
        }else{
            this.go('/login');
        }
    
        
    }

    emit = (event, data) => {
        Messenger.socket().emit(event, data);
    }

    
    ioEventListener = () => {
        const socket = Messenger.socket();
        // dang nhap thanh cong
        socket.on('check-profile-account-success', (user) => {
            let {access_token} = user;
            Messenger.set({user, access_token:access_token});
            let {form} = this.state;
            form.id = user.id;
            form.name = user.name;
            this.setState({form, user});
        });

        socket.on('check-profile-account-fail', (data) => {
            this.go('/login');
        });

        socket.on('profile-update-account-success', (user) => {
            let {access_token} = user;
            Messenger.set({user, access_token:access_token});
            let {form} = this.state;
            form.access_token = user.access_token;
            form.username = user.username;
            form.email = user.email;
            this.setState({form, user});
            alert("Cập nhật thông tin thành công");

        });

        socket.on('profile-update-account-fail', (data) => {
            alert("Cập nhật thành công. Vui lòng đăng nhập lại");
        });
    }

    inputDidUpdate = (e) => {
        let input = e.target;
        let name = input.name;
        let value = input.value;
        let {form} = this.state;
        form[name] = value;
        this.setState({form});
    }

    save = () => {
        let {form} = this.state;
        let errors = [];
        if(!form.username) errors.push('Vui lòng nhập tên truy cập!');
        if(!form.email) errors.push('Vui lòng nhập email!');
        if(!form.password) errors.push('Vui lòng nhập Mật khẩu!');
        if(errors.length){
            return alert(errors.join('\n'));
        }
        Messenger.request('save-account', 'POST', form, (res)=>{
            if(!res.status){
                if(res.errors) alert(res.errors.join(", \n"));
            }else{

                var access_token = res.user.access_token;
                let verify = {
                    data:{access_token: access_token},
                    success:'profile-update-account-success',
                    fail:'profile-update-account-fail',
                }
                this.emit('verify-account', verify);
            }
        });
    }

    go = (path) => {
        this.props.events.go(path);
    }

}



