import React, {Component} from 'react';
export default class Header extends Component{
    constructor(props){
        super(props);
        this.state = {
            nav:[
                {link:'/', text:'Home', active:'home'},
                {link:'/messages', text:'Phòng chat', active:'messages'},
                {link:'/profile', text:'Profile', active:'profile'},
                {link:'/logout', text:'[ Thoát ]', active:'logout'},
                
                
            ]
        };

    }
    render(){
        let {active} = this.props;
        if(!active) active = 'home';
        let nav = this.state.nav.map((el, k) => {
            let cls = 'nav-link'+(el.active == active ? ' active':'');
            return <a className={cls} href={el.link} key={k}>{el.text}</a>;
        });
        return (
            <header className="masthead">
                <div className="inner">
                    <nav className="nav nav-masthead justify-content-center">
                        {nav}
                    </nav>
                </div>
            </header>
        );
    }           
    
    go = path => {
        this.props.history.push(path);
    }
}


