import React, {Component} from 'react';

import Messenger from '../core/Messenger';
import Menu from './Menu';


class Info extends Component{
    constructor(props){
        super(props);
        this.state = {
            user:{},
            form:{
                access_token:"",
                name:"",
                avatar: "",
                avatar_file: "",
                avatar_data: "",
                password:""
                
            }
        };
    }
    render(){
        let {form} = this.state;
        let choose_label = form.avatar_file !== "" ? form.avatar_file : "Chọn ảnh đai diện";
        let {user} = this.state;
        let profile = (!user.access_token)?(<h3>Đang xử lý...</h3>):(
            <div className="profile-form form">
                <div className="row">
                    <div className="col-sm-12 col-md-5 col-lg-4 col-xl-3">
                        <label htmlFor="profile-name">Họ và tên</label>
                    </div>
                    <div className="col-sm-12 col-md-7 col-lg-8 col-xl-9">
                        <div className="form-group">
                            <div className="form-control-input input-group">
                                <div className="input-group-prepend">
                                    <label htmlFor="profile-name" className="input-group-text"><i className="fa fa-user"></i></label>
                                </div>
                                <input 
                                    type="text" 
                                    name="name" 
                                    id="profile-name" 
                                    onChange={this.inputDidUpdate} 
                                    onKeyUp={this.inputDidUpdate} 
                                    className="form-control" 
                                    placeholder="Nhập họ và tên" 
                                    value={form.name} 
                                />
                            </div>
                        </div>
                    </div>
                </div>
                
                <div className="row">
                    <div className="col-sm-12 col-md-5 col-lg-4 col-xl-3">
                        <label htmlFor="profile-avatar">Ảnh đại diện</label>
                    </div>
                    <div className="col-sm-12 col-md-7 col-lg-8 col-xl-9">
                        <div className="form-group">
                            <div className="input-group mb-3">
                                <div className="input-group-prepend">
                                    <span className="input-group-text"><i className="fa fa-picture-o"></i></span>
                                </div>
                                <div className="custom-file">
                                    <input 
                                        type="file" 
                                        accept="image/png, image/jpeg, image/gif" 
                                        onChange={this.chooseAvatar} 
                                        id="profile-user-avatar" 
                                        className="custom-file-input" 
                                    />
                                    <label className="custom-file-label" htmlFor="profile-user-avatar">{choose_label}</label>
                                </div>
                            </div>

                        </div>
                    </div>
                    
                </div>
                <div className="row">
                    <div className="col-sm-12 col-md-5 col-lg-4 col-xl-3">
                        <label htmlFor="profile-password">Mật khẩu hiện tại</label>
                    </div>
                    <div className="col-sm-12 col-md-7 col-lg-8 col-xl-9">
                        <div className="form-group">
                            <div className="form-control-input input-group">
                                <div className="input-group-prepend">
                                    <label htmlFor="profile-password" className="input-group-text"><i className="fa fa-key"></i></label>
                                </div>
                                <input 
                                    type="password" 
                                    name="password" 
                                    id="profile-password" 
                                    onChange={this.inputDidUpdate} 
                                    onKeyUp={this.inputDidUpdate} 
                                    className="form-control" 
                                    placeholder="Nhập Mật khẩu hiện tại" 
                                    value={form.password} 
                                />
                            </div>
                        </div>
                    </div>
                </div>
                
                
                <div className="form-group buttons text-center">
                    <button type="button" className="btn btn-success" onClick={this.save}><i className="fa fa-pencil"></i> Lưu lại</button>
                </div>
            </div>
        );
        return (
            <div className="profile-info mt-3">
                {profile}
            </div>
            
        );
    }

    componentDidMount(){
        this.ioEventListener();
        if(Messenger.checkLogin()){
            let {user} = Messenger;
            let {form} = this.state;
            form.id = user.id;
            form.access_token = user.access_token;
            form.name = user.name;
            form.avatar = user.avatar;
            this.setState({form, user});
        }else if(Messenger.access_token){
            this.emit('verify-account', {
                data:{
                    access_token:Messenger.access_token,
                },
                success:'check-profile-info-success',
                fail:'check-profile-info-fail',
            });
        }else{
            this.go('/login');
        }
    
        
    }

    emit = (event, data) => {
        Messenger.socket().emit(event, data);
    }

    
    ioEventListener = () => {
        const socket = Messenger.socket();
        // dang nhap thanh cong
        socket.on('check-profile-info-success', (user) => {
            let {access_token} = user;
            Messenger.set({user, access_token:access_token});
            let {form} = this.state;
            form.id = user.id;
            form.name = user.name;
            form.avatar = user.avatar;
            this.setState({form, user});
        });

        socket.on('check-profile-info-fail', (data) => {
            this.go('/login');
        });

        socket.on('profile-update-info-success', (user) => {
            let {access_token} = user;
            Messenger.set({user, access_token:access_token});
            let {form} = this.state;
            form.name = user.name;
            form.avatar = user.avatar;
            this.setState({form, user});
            this.props.onChangeInfo(user);
            alert("Cập nhật thông tin thành công");

        });

        socket.on('profile-update-info-fail', (data) => {
            alert("Cập nhật thành công. Vui lòng đăng nhập lại");
        });
    }

    inputDidUpdate = (e) => {
        let input = e.target;
        let name = input.name;
        let value = input.value;
        let {form} = this.state;
        form[name] = value;
        this.setState({form});
    }

    chooseAvatar = (e) => {
        let input = e.target;
        if (window.File && window.FileList && window.FileReader){
            let files = input.files;
            if(files.length){
                let file = files[0];
                if (file.type.indexOf("image") == 0) {
                    var fileReader = new FileReader();
                    fileReader.onload = (f) => {

                        let {form} = this.state;
                        form.avatar = f.target.result;
                        form.avatar_data = f.target.result;
                        form.avatar_file = file.name;
                        this.setState({form})
                    };

                    fileReader.readAsDataURL(file);
                }
                else{
                    alert("File không hợp lệ");
                }
            }
        }
        else{
            alert('trình duyệt không hổ trọ upload file');
        }
    }

    save = () => {
        let {form} = this.state;
        let errors = [];
        if(!form.name) errors.push('Vui lòng nhập họ tên!');
        if(!form.password) errors.push('Vui lòng nhập Mật khẩu!');
        if(errors.length){
            return alert(errors.join('\n'));
        }
        Messenger.request('save-info', 'POST', form, (res)=>{
            if(!res.status){
                if(res.errors) alert(res.errors.join(", \n"));
            }else{

                var access_token = res.user.access_token;
                let verify = {
                    data:{access_token: access_token},
                    success:'profile-update-info-success',
                    fail:'profile-update-info-fail',
                }
                this.emit('verify-account', verify);
            }
        });
    }

    go = path => {
        this.props.events.go(path);
    }
}

export default Info;


