import React, {Component} from 'react';

export default class Menu extends Component{
    constructor(props){
        super(props);
        this.state = {
            nav:[
                {link:'/profile/info', text:'Thay tên đổi họ', tab:'info', active:'info'},
                {link:'/profile/account', text:'Thông tin tài khoản', tab:'account', active:'account'},
                {link:'/profile/password', text:'Đổi mật khẩu', tab:'password', active:'password'},
                
            ]
        };

    }
    render(){
        let {active} = this.props;
        if(!active) active = 'home';
        let nav = this.state.nav.map((el, k) => {
            let cls = 'nav-link'+(el.active == active ? ' active':'');
            return (
                <li className="nav-item" key={k}>
                    <a  className={cls} 
                        href="javascript:void(0)" 
                        data-tab={el.tab} 
                        onClick={(e)=>this.selectTab(e.target.getAttribute('data-tab'))}>{el.text}</a>
                </li>
            );
        });
        return (
            <aside className="sidebar">
                <nav className="nav d-md-flex">
                    {nav}
                </nav>
            
            </aside>
        );
    }           
    
    go = path => {
        this.props.history.push(path);
    }
    selectTab = (tab) => {
        this.props.onSelectTab(tab);
    }
}


