import React, {Component} from 'react';

import Messenger from '../core/Messenger';
import Menu from './Menu';


export default class Password extends Component{
    constructor(props){
        super(props);
        this.state = {
            user:{},
            form:{
                access_token:"",
                password_current:"",
                password:"",
                password_confirmation:""
                
            }
        };
    }
    render(){
        let {form} = this.state;
        let {user} = this.state;
        let profile = (!user.access_token)?(<h3>Đang xử lý...</h3>):(
            <div className="profile-form form">
                <div className="row">
                    <div className="col-sm-12 col-md-5 col-lg-4 col-xl-3">
                        <label htmlFor="profile-password_current">Mật khẩu hiện tại</label>
                    </div>
                    <div className="col-sm-12 col-md-7 col-lg-8 col-xl-9">
                        <div className="form-group">
                            <div className="form-control-input input-group">
                                <div className="input-group-prepend">
                                    <label htmlFor="profile-password_current" className="input-group-text"><i className="fa fa-key"></i></label>
                                </div>
                                <input 
                                    type="password" 
                                    name="password_current" 
                                    id="profile-password_current" 
                                    onChange={this.inputDidUpdate} 
                                    onKeyUp={this.inputDidUpdate} 
                                    className="form-control" 
                                    placeholder="Nhập Mật khẩu hiện tại" 
                                    value={form.password_current} 
                                />
                            </div>
                        </div>
                    </div>
                </div>
                
                <div className="row">
                    <div className="col-sm-12 col-md-5 col-lg-4 col-xl-3">
                        <label htmlFor="profile-password">Mật khẩu mới</label>
                    </div>
                    <div className="col-sm-12 col-md-7 col-lg-8 col-xl-9">
                        <div className="form-group">
                            <div className="form-control-input input-group">
                                <div className="input-group-prepend">
                                    <label htmlFor="profile-password" className="input-group-text"><i className="fa fa-lock"></i></label>
                                </div>
                                <input 
                                    type="password" 
                                    name="password" 
                                    id="profile-password" 
                                    onChange={this.inputDidUpdate} 
                                    onKeyUp={this.inputDidUpdate} 
                                    className="form-control" 
                                    placeholder="Nhập Mật khẩu mới" 
                                    value={form.password} 
                                />
                            </div>
                        </div>
                    </div>
                </div>
                
                <div className="row">
                    <div className="col-sm-12 col-md-5 col-lg-4 col-xl-3">
                        <label htmlFor="profile-password_confirmation">Mật khẩu xác nhận</label>
                    </div>
                    <div className="col-sm-12 col-md-7 col-lg-8 col-xl-9">
                        <div className="form-group">
                            <div className="form-control-input input-group">
                                <div className="input-group-prepend">
                                    <label htmlFor="profile-password_confirmation" className="input-group-text"><i className="fa fa-lock"></i></label>
                                </div>
                                <input 
                                    type="password" 
                                    name="password_confirmation" 
                                    id="profile-password_confirmation" 
                                    onChange={this.inputDidUpdate} 
                                    onKeyUp={this.inputDidUpdate} 
                                    className="form-control" 
                                    placeholder="Nhập lại mật khẩu" 
                                    value={form.password_confirmation} 
                                />
                            </div>
                        </div>
                    </div>
                </div>
                
                
                <div className="form-group buttons text-center">
                    <button type="button" className="btn btn-success" onClick={this.save}><i className="fa fa-check"></i> Lưu lại</button>
                </div>
            </div>
        );
        return (
            <div className="profile-info mt-3">
                {profile}
            </div>
            
        );
    }

    componentDidMount(){
        this.ioEventListener();
        if(Messenger.checkLogin()){
            let {user} = Messenger;
            let {form} = this.state;
            form.id = user.id;
            form.access_token = user.access_token;
            form.name = user.name;
            form.avatar = user.avatar;
            this.setState({form, user});
        }else if(Messenger.access_token){
            this.emit('verify-account', {
                data:{
                    access_token:Messenger.access_token,
                },
                success:'check-profile-password-success',
                fail:'check-profile-password-fail',
            });
        }else{
            this.go('/login');
        }
    
        
    }

    emit = (event, data) => {
        Messenger.socket().emit(event, data);
    }

    
    ioEventListener = () => {
        const socket = Messenger.socket();
        // dang nhap thanh cong
        socket.on('check-profile-password-success', (user) => {
            let {access_token} = user;
            Messenger.set({user, access_token:access_token});
            let {form} = this.state;
            form.id = user.id;
            this.setState({form, user});
        });

        socket.on('check-profile-password-fail', (data) => {
            this.go('/login');
        });

        socket.on('profile-update-password-success', (user) => {
            let {access_token} = user;
            Messenger.set({user, access_token:access_token});
            let {form} = this.state;
            form.name = user.name;
            form.avatar = user.avatar;
            this.setState({form, user});
            let confirmLogout = window.confirm("Thay đổi mật khẩu thành công!\n Bạn có muốn đăng nhập lại không?");
            if(confirmLogout){
                this.go('/logout');
            }

        });

        socket.on('profile-update-password-fail', (data) => {
            alert("Cập nhật thành công. Vui lòng đăng nhập lại");
        });
    }

    inputDidUpdate = (e) => {
        let input = e.target;
        let name = input.name;
        let value = input.value;
        let {form} = this.state;
        form[name] = value;
        this.setState({form});
    }

    save = () => {
        let {form} = this.state;
        let errors = [];
        if(!form.password_current) errors.push('Vui lòng nhập mật khẩu hiện tại!');
        if(!form.password) errors.push('Vui lòng nhập mật khẩu mới');
        if(!form.password_confirmation) errors.push('Vui lòng nhập Mật khẩu xác nhận!');
        if(errors.length){
            return alert(errors.join('\n'));
        }else if(form.password !== form.password_confirmation){
            return alert("Mật khẩu không khớp");
        }
        Messenger.request('save-password', 'POST', form, (res)=>{
            if(!res.status){
                if(res.errors) alert(res.errors.join(", \n"));
            }else{

                var access_token = res.user.access_token;
                let verify = {
                    data:{access_token: access_token},
                    success:'profile-update-password-success',
                    fail:'profile-update-password-fail',
                }
                this.emit('verify-account', verify);
            }
        });
    }

    go = path => {
        this.props.events.go(path);
    }
}



