import React, { Component } from 'react';
import Messenger from './core/Messenger';
import Header from './components/Header';
import ProfileInfo from './components/ProfileInfo';

class Profile extends Component {
    constructor(props){
        super(props);
        this.state = {
            user:{
                name: "Đang xử lý...",
                email:"Please wait..."
            }
        };
    }
    render() {
        let events = {
            go: this.go
        };
        let {user} = this.state;
        let tab = (!user.access_token)? (
            <div>
                <h1 className="text-center">{user.name}</h1>
                <p className="text-center">{user.email}</p>   
            </div>
        ) : (
            <div>
                <Header active="home" events={events} />
                <ProfileInfo events={events} />
            </div>
        );
        return (
            <div className="lab lab0">
                <div className="lab-content">
                    {tab}
                </div>
            </div>
        );
    }
    
    componentDidMount(){
        this.ioEventListener();
        if(Messenger.checkLogin()){
            this.setState({user:Messenger.user});
        }else if(Messenger.access_token){
            this.emit('verify-account', {
                data:{
                    access_token:Messenger.access_token,
                },
                success:'check-auth-profile-success',
                fail:'check-auth-profile-fail',
            });
        }else{
            this.props.history.push('/login');
        }
    
        
    }

    emit = (event, data) => {
        Messenger.socket().emit(event, data);
    }

    
    ioEventListener = () => {
        const socket = Messenger.socket();
        // dang nhap thanh cong
        socket.on('check-auth-profile-success', (user) => {
            let {access_token} = user;
            Messenger.set({user, access_token:access_token});
            this.setState({user});
        });

        socket.on('check-auth-profile-fail', (data) => {
            this.props.history.push('/login');
        });
    }
    go = (path) => {
        this.props.history.push(path);
    }
}

export default Profile;



